import React, {useState} from "react";
import RightSideMenuDrawer from "./RightSideMenuDrawer";
import NavigationBar from "./NavigationBar";
import LeftSideMenuDrawer from "./LeftSideMenuDrawer";
import {Route, Router, Switch} from "react-router-dom";
import SignInPage from "../../views/Auth/SignInPage";
import {createBrowserHistory} from "history";
import { makeStyles } from '@material-ui/core/styles';
import {Container, CssBaseline, Toolbar} from "@material-ui/core";
import SignUpPage from "../../views/Auth/SignUpPage";
import LandingPage from "../../views/LandingPage/LandingPage";

const drawerWidth = 240;

let hist = createBrowserHistory();
const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
    },
    appBar: {
        zIndex: theme.zIndex.drawer + 1,
    },
    drawer: {
        width: drawerWidth,
        flexShrink: 0,
    },
    drawerPaper: {
        width: drawerWidth,
    },
    drawerContainer: {
        overflow: 'auto',
    },
    content: {
        flexGrow: 1,
        padding: theme.spacing(3),
    },
    logo: {
        //paddingLeft: '0.5em',
        paddingRight: '1em',
    }
}));

const NavigationContainer = () => {

    const classes = useStyles();
    const [drawerState, drawerStateOpen] = useState(true);
    return (
        <div className={classes.root}>
            <CssBaseline />
            <NavigationBar drawerState={drawerState}
                           drawerStateOpen={drawerStateOpen}
                           classes={classes}
            />
            <LeftSideMenuDrawer drawerState={drawerState}
                                drawerStateOpen={drawerStateOpen}
                                classes={classes}

            />

            <main className={classes.content}>
                <Toolbar />
                <Container maxWidth="lg">
                    <Router history={hist}>
                        <Switch>
                            <Route exact path="/" component={LandingPage} />
                            <Route path="/signin" component={SignInPage} />
                            <Route path="/signup" component={SignUpPage} />
                        </Switch>
                    </Router>
                </Container>
            </main>

            <RightSideMenuDrawer drawerState={drawerState}
                                 drawerStateOpen={drawerStateOpen}
                                 classes={classes}
            />
        </div>
    );
};
export default NavigationContainer;
